/* GLOBAL */
body, html {
    padding: 0;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    font-size: 16px;
}

* {
    box-sizing: border-box;
}

.c-container {
    max-width: 1750px;
    margin: auto;
    padding-left: 15px;
    padding-right: 15px;
}

ul, li {
    list-style: none;
    padding: 0;
    margin: 0;
}

a, a:hover {
    text-decoration: none !important;
    color: inherit;
}

/* BUTTONS */
.c-btn {
    display: inline-block;
    border-radius: 4px;
    transition: 0.2s;
    background: #f21559;
    padding: 18px 46px;
    font-size: 16px;
    color: rgb(255, 255, 255);
    font-family: "Lato";
    font-weight: 900;
    text-decoration: none;
    cursor: pointer;
    border: none;
}

.btn--wide {
    padding: 18px 107px;
}

.btn--full {
    width: 100%;
    display: block;
    padding: 18px;
}

.btn--secondary {
    background: #0965bc;
    background: #0098FF;
}

.btn--secondary:hover {
    color: #fff;
    background: #0965BC;
}

.btn--back {
    color: #3fb1ff;
    font-size: 13px;
    font-family: "Lato";
    font-weight: 300;
    text-transform: uppercase;
    padding: 17px 0 17px;
    display: block;
    text-align: left;
}

.lbox__buttons .btn {
    margin: 0 auto 10px;
    display: block;
    width: calc(100% - 10px);
    display: block;
}


.btn--primary {
    transition: 0.2s;
}

.btn--primary:hover {
    color: #fff;
    transition: 0.2s;
    background: rgba(248, 43, 106, 0.9);
}

.bw .btn--primary:hover {
    background: rgb(248, 43, 106);
}

.lbox__buttons .btn--primary {
    background: #F21559;
}

.lbox__buttons .btn--primary:hover {
    background: #BB1456;
}

/* ICONS */
.icon {
    background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 1px;
    min-width: 1px;
}

.icon--arrow-next {
    background-image: url('../../public/img/next.svg');
    width: 22px;
    height: 18px;
}

/* main parts */
#c-header, #footer, .filters {
    z-index: 9;
    position: relative;
}

#c-header.header--fixed {
    position: fixed;
    background: #fff;
    width: 100%;
    z-index: 1000;
}

.filters {
    box-shadow: 0px 4px 8px 1px #ddd;
}

.box-shadow {
    box-shadow: 0px 0px 6px 2px #eee;
}


/* NAVBAR */
.c-navbar {
    padding: 10px 0 10px;
}

.navbar__grid {
    display: -ms-grid;
    display: grid;
    grid-gap: 20px 20px;
    padding: 10px 0;
}

.navbar__el {
    text-align: center;
}

.logo--header {
    max-height: 40px;
}

.navbar__el--mobile {
    display: block;
    position: fixed;
    width: 100%;
    height: calc(100% - 100px);
    top: 100px;
    left: 0;
}

.menu--mobile {
    height: 0px;
    transition: 1s;
    overflow: hidden;
}

.menu--mobile ul {
    overflow-y: scroll;
    margin-right: -20px;
    margin-left: -20px;
    padding: 20px;
    padding-top: 120px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.menu--mobile .menu__a {
    font-size: 18px;
    padding: 20px;
}

.menu--mobile .menu__el {
    margin: 10px;
}

.menu--mobile .menu__el .btn {
    margin-top: 10px;
}


#c-navbar.active .menu--mobile {
    height: auto;
    top: 0;
    transition: 1s;
    position: absolute;
    padding: 20px;
    /* overflow: scroll; */
    width: 100%;
    height: calc(100vh - 100px);
    background: #fff;
    left: 0;
}

.navbar__el--ham {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: flex-end;
    position: relative;
}

.hamburger {
    position: relative;
    display: flex;
    position: relative;
    flex-direction: column;
    height: 40px;
    width: 50px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    cursor: pointer;
}

.hamburger span {
    width: 100%;
    height: 3px;
    background: #333;
    transition: 0.5s;
    position: absolute;
}

.hamburger span:nth-child(1) {
    top: 0%;
}

.hamburger span:nth-child(2) {
    opacity: 1;
    top: 50%;
}

.hamburger span:nth-child(3) {
    top: 100%;
}

#c-navbar.active .hamburger span {
    transition: 0.5s;
}

#c-navbar.active .hamburger span:nth-child(1) {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    position: absolute;
    top: 50%;
}

#c-navbar.active .hamburger span:nth-child(2) {
    opacity: 0;
}

#c-navbar.active .hamburger span:nth-child(3) {
    transform: rotate(-225deg);
    -webkit-transform: rotate(-225deg);
    -moz-transform: rotate(-225deg);
    -o-transform: rotate(-225deg);
    position: absolute;
    top: 50%;
    -webkit-transform: rotate(-225deg);
    left: 0;
}

.navbar__el--mobile {
    position: fixed;
}

@media only screen and (min-width: 240px) and (max-width: 640px) {
    /* hamburger*/
    .space-header {
        display: none;
    }

    .navbar__el--page-brand {
        grid-area: page-brand;
        text-align: left;
    }

    .navbar__el--logo-ip {
        grid-area: logo-ip;
        display: none;
    }

    .navbar__el--menu {
        grid-area: menu;
        display: none;
    }

    .navbar__el--btn {
        grid-area: btn;
        display: none;
    }

    .navbar__el--ham {
        grid-area: ham;
    }

    .navbar__el--mobile {
        grid-area: mobile;
        height: 0;
    }

    .navbar__grid {
        display: -webkit-flex;
        display: flex;
        width: 100%;
        justify-content: space-between;
    }

    .workflow__title {
        text-align: center;
    }

    .menu--mobile .menu__el .btn, .btn--wide {
        width: 100%;
        padding: 18px 0;
        display: block;
        margin: 0 auto;
        max-width: 300px;
    }

    .title span, .workflow__subtitle span {
        display: block;
        width: 100%;
    }

