         display: -ms-grid;
         display: grid;
         grid-gap: 20px 20px;
         padding: 10px 0;
         grid-template-columns: auto auto;
         grid-template-areas:
         "page-brand logo-ip"
         "menu menu"
         "btn btn";
     }*/
}

@media only screen and (min-width: 640px) {
    .navbar__el--ham {
        display: none;
    }

    .navbar__el--mobile {
        display: none;
    }
}

@media only screen and (min-width: 640px) and (max-width: 1365px) {
    .navbar__el--page-brand {
        grid-area: page-brand;
    }

    .navbar__el--logo-ip {
        grid-area: logo-ip;
    }

    .navbar__el--menu {
        grid-area: menu;
    }

    .navbar__el--btn {
        grid-area: btn;
    }

    .navbar__grid {
        display: -ms-grid;
        display: grid;
        grid-gap: 20px 20px;
        padding: 10px 0;
        grid-template-columns: auto auto;
        grid-template-areas:
        "page-brand logo-ip btn"
        "menu menu menu";
    }

    .menu__el {
        display: inline-block;
    }
}

@media only screen and (min-width: 1366px) {
    .navbar__grid {
        display: -ms-grid;
        display: grid;
        grid-gap: 20px 20px;
        padding: 10px 0;
        grid-template-columns: auto auto auto auto;
    }

    .menu__el {
        display: inline-block;
    }
}

.navbar__logo--ip {
    position: relative;
    top: 10px;
    width: 38px;
    height: 46px;
    background: url(../../public/img/logo-ip-gray.png);
    transition: .3s;
    margin: 0 auto;
}

.navbar__logo--ip:hover {
    background: url(../../public/img/logo-ip-color.png);
    transition: .3s;
}

.navbar__el--menu .menu {
    position: relative;
    top: 10px;
}

.navbar__el--btn .btn {
    position: relative;
    top: 5px;
}


/* .page-brand */
.page-brand {
    clear: both;
    overflow: hidden;
    display: inline-block;
}

.page-brand__img {
    float: left;
    margin-right: 6px;
    position: relative;
}

.page-brand__title {
    float: left;
    margin: 6px 0 0;
    font-size: 16px;
    line-height: 16px;
    color: rgb(24, 15, 76);
    font-family: "Lato";
    font-weight: 300;
    text-align: left;
}

.page-brand__line {
    display: block;
}

.page-brand__line--strong {
    font-weight: 900;
    font-size: 26px;
    line-height: 22px;
}

/* .menu */
.menu__a {
    font-size: 13px;
    letter-spacing: 1px;
    padding: 15px;
    display: inline-block;
    color: rgb(0, 0, 0);
    font-family: "Lato";
    font-weight: bold;
    text-transform: uppercase;
    transition: 0.3s;
}

.menu__active-item, .menu__a:hover {
    color: #0686d8;
    transition: 0.3s;
}


/* SLIDER */
.slider {
    position: relative;
    width: 100%;
    min-height: 100vh;
}

.slider__el {

    position: absolute;
    top: 0;
    xleft: -100vw;
    left: 0;
    width: 100%;
    z-index: 1;
    height: 100vh !important;
}

.slider__el.active {
    position: relative;
    left: 0;
    z-index: 10;
    height: 100vh !important;

}

.slider__el .intro {
    opacity: 0;
    transition: 0.5s;
}

.slider__el.active .intro {
    opacity: 1;
    transition: 0.5s;
}


/* INTRO */
.intro {
    height: 100%;
}

.intro * {
    word-break: break-word;
}

.intro {
    display: block;
    overflow: hidden;
    background-color: #170f4b;
    background-image: url('../../public/img/bck21.jpg');
    background-image: url('../../public/img/baner-top-a.jpg');
    background-position: 50% 50%;
    background-size: cover;
    padding: 140px 0 250px;
    overflow: hidden;
    position: relative;
}

.intro--2 {
    background-color: #3a2c90;
    background-image: url('../../public/img/baner-top-b.jpg');
}

.intro__content {
    background-image: url("../../public/img/stars.png");
    background-position: 50% 50%;
    background-size: cover;
    padding: 170px 0 90px
}

.intro__subtitle {
    font-size: 26px;
    line-height: 50px;
    color: rgb(255, 255, 255);
    font-family: "Lato";
    font-weight: 300;
    text-align: center;
    margin: 0;
}

.intro__title {
    font-size: 45px;
    line-height: 56px;
    color: rgb(255, 255, 255);
    font-family: "Lato";
    font-weight: 300;
    text-align: center;
    margin: 0;
}

.intro__date {
    font-size: 37px;
    line-height: 60px;
    color: rgb(255, 255, 255);
    font-family: "Lato";
    font-weight: 900;
    text-align: center;
    margin: 0;
}

.intro--2 .intro__subtitle {
    margin-top: 15px;
    line-height: 35px;
}

.intro__float {
    position: absolute;
    top: 0;
    width: 100%;
}

.intro__float--top {
    top: 0;
}

.intro__float--bottom {
    bottom: 0;
    top: unset;
}

.intro__sep {
    background: #fff;
    height: 150px;
    width: 1px;
    display: block;
    margin: auto;
}

.intro__img {
    margin: auto;
    margin: auto;
    position: absolute;
    top: -270px;
    max-width: 100%;
    left: calc(50% - 375px);
}

@media only screen and (min-width: 380px) {
