/* box */
.box {
    text-align: center;
    padding: 16px 3px 16px;
    display: block;
    min-height: 240px;
}

.box__row--img {
    position: relative;
    display: inline-block;
    margin: auto;
    height: 90px;
    max-height: 90px;
}

.box__row--img img {
    max-height: 100%;
    display: block;
}

.box__row--content {
    overflow: hidden;
}

.box__count {
    position: absolute;
    position: absolute;
    top: -30px;
    left: -30px;
}

.box__txt {
    font-size: 18px;
    line-height: 24px;
    color: rgb(0, 0, 0);
    font-family: "Lato";
    font-weight: 300;
    text-align: center;
    margin: 5px 0 10px;
}

.boxes {
    display: -ms-grid;
    display: grid;
    grid-gap: 16px 20px;

}

.boxes__el {

}

