    display: grid;
}

.workflow__parts {
    margin: 60px auto 10px;
}

@media only screen and (min-width: 768px) {
    .workflow__parts, .info__parts {
        grid-template-columns: 50% 50%;
    }

    .workflow__part, .info__part {
        padding: 0 50px;
    }
}

.workflow .c-row {
    margin-bottom: 18px;
}

.workflow__title {
    font-size: 37px;
    color: rgb(24, 15, 76);
    font-family: "Lato";
    font-weight: 300;
    margin: 0;
}

.workflow__title strong {
    font-weight: 900;
}

.workflow__subtitle {
    font-size: 26px;
    color: rgb(0, 151, 255);
    font-family: "Lato";
    font-weight: bold;
    margin: 0;
}

.workflow__list {
    margin-top: 8px;
    position: relative;
    margin-left: 60px;
    list-style-image: url('../../public/img/list1.png');
}

.workflow__li {
    position: relative;
    font-size: 18px;
    margin-bottom: 25px;
    color: rgb(24, 15, 76);
    font-family: "Lato";
    font-weight: 300;
}


.workflow__li:after,
.workflow__li:before {
    content: '';
    display: block;
    position: absolute;
    background-position: 50% 50%;
    background-size: contain;
    background-repeat: no-repeat;
    top: 0;
}

.workflow__li:after {
    width: 20px;
    height: 20px;
    left: -50px;
    background-image: url('../../public/img/list1.png');
}

.workflow__li:last-of-type:after {
    background-image: url('../../public/img/list1-active.png');
}

.workflow__li:before {
    content: '';
    display: block;
    position: absolute;
    height: calc(100% + 25px);
    width: 1px;
    background: #5cbdff;
    left: -40px;
}

.workflow__li:last-of-type:before {
    height: 0;
}

.workflow__subs {
    margin-left: 60px;
}

.workflow__sub {
    display: block;
    font-size: 13px;
    color: rgb(24, 15, 76);
    font-family: "Lato";
    font-weight: 300;
}

/* 1 col */
.workflow__parts {
    display: -webkit-flex;
    display: -ms-flex;
