.button {
    background: #F21559;
    color: #fff;
    font-size: 1rem;
    font-weight: 900;
    padding: 6px 12px;
    display: inline-block;
    border-radius: 5px;
}

.button:hover {
    background: #a10e3b;
    color: #fff;
}

#bottom-contact-trigger {
    position: fixed;
    bottom: 20px;
    left: 20px;
    background: #f1c119;
    color: #333;
    padding: 15px;
    font-size: 30px;
    border-radius: 8px 34px 34px 34px;
    width: 60px;
    height: 60px;
    line-height: 30px;
    text-align: center;
    z-index: 10;
    box-shadow: 0 5px 4px 0 rgba(0, 0, 0, .26);
    transition: ease all 250ms
}

#fc_frame {
    transition: ease all 250ms
}

#bottom-contact-trigger.active, #fc_frame.active {
    transform: translateY(-105px)
}

#bottom-contact-trigger:hover {
    background: #fbc817
}

#bottom-contact {
    background-color: transparent; /* Black background color */
    position: fixed; /* Make it stick/fixed */
    display: none;
    bottom: 0; /* Stay on top */
    width: 100%; /* Full width */
    transition: bottom .7s; /* Transition effect when sliding down (and up) */
    z-index: 99;
}

.conf {
    background-size: cover;
    width: 100%;
}

@media screen and (max-width: 769px) {
    .conf-tablet, .conf-desktop {
        display: none;
    }

    .conf-mobile {
        display: block;
        padding-top: 107px;
    }
}

